import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import { registerAsync } from "../../store/userActions";
import { useForm } from "react-hook-form";
import * as yup from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import {
  makeStyles,
  Paper,
  Typography,
  InputLabel,
  Button,
} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  paper: {
    width: "50%",
    backgroundColor: "#191d3a",
    padding: "2rem",
    margin: "0 auto",
    [theme.breakpoints.down("sm")]: {
      width: "85%",
    },
  },
  title: {
    padding: "0.5rem 1rem",
    fontWeight: "bold",
    fontSize: "3rem",
    textAlign: "center",
    color: "#ec5990",
  },
  input: {
    marginTop: "0.5rem",
    marginBottom: "0.5rem",
    padding: "0.5rem 1rem",
    backgroundColor: "#1e2a4a",
    color: "#fff",
    width: "100%",
    borderRadius: "1rem",
    border: "2px solid #fff",
    "&:focus": {
      outline: "none",
      borderColor: "#ff7aa8",
    },
    "&::placeholder": {
      color: "#4f6294",
    },
  },
  SubmitButton: {
    display: "block",
    marginBottom: "1rem",
    backgroundColor: "#ec5990",
    "&:hover": {
      backgroundColor: "#ec5990",
    },
  },
  loginLink: {
    color: "#bf1650",
  },
  error: {
    padding: "0.5rem",
    color: "#bf1650",
  },
  label: {
    color: "#fff",
    fontWeight: "bold",
    paddingTop: "0.5rem",
  },
  question: {
    display: "inline-block",
    color: "#fff",
    marginRight: "0.5rem",
  },
}));

function RegisterPage() {
  const classes = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();
  const [isLoading, setIsLoading] = useState(false);

  const schema = yup.object().shape({
    email: yup
      .string()
      .required(" ny khng c trng")
      .email("ti khon khng ng nh dng"),
    fullname: yup
      .string()
      .required(" ny khng c trng")
      .max(20, "ti a 20 k t"),
    password: yup
      .string()
      .required(" ny khng c trng")
      .min(6, "mt khu t nht 6 k t"),
    repassword: yup
      .string()
      .required(" ny khng c trng")
      .min(6, "mt khu t nht 6 k t")
      .oneOf([yup.ref("password"), null], "mt khu khng trng"),
  });

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    resolver: yupResolver(schema),
  });

  const onSubmit = async (data) => {
    if (isLoading) return;

    setIsLoading(true);

    await dispatch(registerAsync(data)).then((res) => {
      if (res.ok) history.push("/login");
      setIsLoading(false);
    });
  };

  return (
    <Paper elevation={3} className={classes.paper}>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Typography className={classes.title}>ng K</Typography>
        <InputLabel className={classes.label}>Tn ng nhp</InputLabel>
        <input
          className={classes.input}
          placeholder="nhp email..."
          {...register("email")}
        />
        {errors.email?.message && (
          <p className={classes.error}>{errors.email?.message}</p>
        )}
        <InputLabel className={classes.label}>Tn ngi dng</InputLabel>
        <input
          className={classes.input}
          placeholder="tn ca bn"
          {...register("fullname")}
        />
        {errors.fullname?.message && (
          <p className={classes.error}>{errors.fullname?.message}</p>
        )}

        <InputLabel className={classes.label}>Mt khu</InputLabel>
        <input
          className={classes.input}
          placeholder="nhp mt khu..."
          type="password"
          {...register("password")}
        />
        {errors.password?.message && (
          <p className={classes.error}>{errors.password?.message}</p>
        )}

        <InputLabel className={classes.label}>Xc nhn mt khu</InputLabel>
        <input
          className={classes.input}
          placeholder="nhp li mt khu... "
          type="password"
          {...register("repassword")}
        />
        {errors.repassword?.message && (
          <p className={classes.error}>{errors.repassword?.message}</p>
        )}

        <Button
          color="primary"
          variant="contained"
          className={classes.SubmitButton}
          onClick={handleSubmit(onSubmit)}
        >
          submit
        </Button>
        <p className={classes.question}>Bn  c ti khon ?</p>
        <Link to="/login" className={classes.loginLink}>
          ng nhp
        </Link>
      </form>
    </Paper>
  );
}

export default RegisterPage;
